import React from 'react'

export class Counter extends React.Component {

    constructor() {
        super()
        this.state = {
            in1: 1,
            in2: 1,
            result: 0
        }
    }


    onin = (e) => {
        this.setState((prev) => ({in1: parseInt(e.target.value)}))
    }
    onin1 = (e) => {
        this.setState((prev) => ({in2: parseInt(e.target.value)}))
    }
    onPlus = () => {
        this.setState(() => ({result: parseInt(this.state.in1) + parseInt(this.state.in2)}))
    }
    onMinus = () => {
        this.setState(() => ({result: parseInt(this.state.in1) - parseInt(this.state.in2)}))
    }
    onMult = () => {
        this.setState(() => ({result: parseInt(this.state.in1) * parseInt(this.state.in2)}))
    }
    onDivis = () => {
        this.setState(() => ({result: parseInt(this.state.in1) / parseInt(this.state.in2)}))
    }
    render() {
        return <div>
            <h1>Calculate 1</h1>
            <input type="number" defaultValue={this.state.in1} onChange={this.onin}/>
            <button onClick={this.onPlus}>+</button>
            <button onClick={this.onMinus}>-</button>
            <button onClick={this.onMult}>*</button>
            <button onClick={this.onDivis}>/</button>
            <input type="number" defaultValue={this.state.in2} onChange={this.onin1}/>
            <h1 style={{textAlign: 'center', width: 420}}>sum:{this.state.result}</h1>
            </div>
    }
}
